<template>
  <div id="home-component" class="container-fluid" style="background: #e3e5eb;">
    <div class="row" style="background: #e3e5eb;">
      <div v-for="(item, index) in listCard" :key="index" class="col-lg-3 col-md-6 col-sm-12 pt-0">
        <CardHome :item="item" />
      </div>
    </div>
    <div class="row" style="background: #e3e5eb;">
      <div class="w-100 d-flex flex-wrap">
        <div class="col-lg-6 col-md-6 col-sm-12">
          <v-card height="100%">
            <div class="header-card" style="background: rgba(0,0,0,.875);">
              <div class="sub-header-card">
                <p class="h6">Cuenta</p>
              </div>
            </div>
            <div class="avatar">
              <img :src="user.imagen" :alt="user._id" />
            </div>
            <div class="content-card">
              <h5>{{ user.nombres }} {{ user.apellido_paterno }} {{ user.apellido_materno }}</h5>
              <p class="text-muted">
                <small>{{ user.rol_usuario }}</small>
              </p>
              <div class="buttons-section">
                <button class="btn btn-dark" @click="signOut">SIGN OUT</button>
                <button class="btn btn-secondary" @click="profile">PROFILE</button>
              </div>
            </div>
            <div class="content-card">
              <div class="container">
                <div class="row content-card">
                  <div class="col-lg-8 col-sm-12">
                    <table class="table">
                      <tbody>
                        <tr>
                          <td class="text-bold">Level</td>
                          <td class="text-right">{{ user.rol_usuario }}</td>
                        </tr>
                        <tr>
                          <td class="text-bold">Telefono</td>
                          <td class="text-right">{{ user.telefono }}</td>
                        </tr>
                        <tr>
                          <td class="text-bold">Edad</td>
                          <td class="text-right">{{ user.edad }}</td>
                        </tr>
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </v-card>
        </div>
        <div class="col-lg-6 col-md-6 col-sm-12">
          <v-card>
            <div class="header-card-clima" style="background:rgba(0,0,0,.875);">
              <div class="container p-5 mb-4">
                <div class="d-flex flex-column">
                  <p class="h6"> {{ dia }} </p>
                  <p>{{ fechaActual }}</p>
                  <hr style="background: #fff;" />
                  <div class="d-flex justify-content-between">
                    <div>
                      <p class="h2 mb-0">{{ clima.temperatura }}°C</p>
                      <p>
                        <small>{{ clima.descripcion}}</small>
                      </p>
                    </div>
                    <i class="fas fa-cloud-sun text-white" style="font-size: 60px;"></i>
                  </div>
                </div>
              </div>
            </div>
          </v-card>

          <v-card>
            <div class="header-card-clima" style="background:#2980b9;">
              
                <v-date-picker
                    color="rgba(0,0,0,.875)"
                    v-model="dateN"
                    full-width
                    :landscape="$vuetify.breakpoint.smAndUp"
                    class="mt-2"
                    
                ></v-date-picker>
              
            </div>
          </v-card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import CardHome from "../../../components/IntranetComponents/Cards/HomeCard";
import { mapState } from "vuex";
export default {
  components: {
    CardHome
  },
  data: () => ({
    dias: ["Lunes", "Martes", "Miercoles", "Jueves", "Viernes", "Sabado","Domingo"],
    fechaActual: new Date(),
    dateN: new Date().toISOString().substr(0, 10),
    dia: ''
  }),
  computed: {
    ...mapState(["listCard", "user", "clima"])
  },
  mounted() {
    this.dia = this.dias[this.fechaActual.getDay()-1]; 
    this.$store.dispatch("getClimaAdmin");
    this.$store.dispatch("updateGlobalIndex");
    setInterval(() => {
      const reloj = this.fechaActual.getTime() + 1000;
      this.fechaActual = new Date(reloj);
    }, 1000);
  },
  methods: {
      profile(){
        this.$router.push('/app1/intranet/myaccount').catch(err => {});
      },
      signOut(){
        localStorage.removeItem('iToken');
        this.$router.push('/app1/login').catch(err => {});
          
      }
  }
};
</script>

<style>
.avatar {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px 0px;
  margin-top: -120px;
}
.avatar img {
  max-width: 200px;
  max-height: 200px;
  border-radius: 50%;
}

.content-card {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
}

.buttons-section {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.buttons-section button {
  margin: 0 10px;
  margin-bottom: 10px;
}

.text-bold {
  font-weight: bold;
}

.header-card-clima {
  color: #fff;
}
</style>